<template>
  <page menu accountBtn>
    <div slot="top" class="top-inner">
      <div class="bnp-logo"><img src="../assets/logo-white.png" /></div>
      <div class="name">Binanswap Pool</div>
    </div>

    <div class="card-items">
      <card class="cions">
        <ul class="harvest">
          <li v-for="item in harvest" :key="item.id">
            <i class="icon"><img :src="item.ico" :alt="item.id" /></i>
            <span class="num" v-if="isLogin">
              <CountUp :endVal="item.num.toNumber()"
            /></span>
            <span class="num" v-else>Locked</span>
            <span
              class="help"
              :class="{ active: !!item.num.toNumber() }"
              @click="doHarvest(item.id)"
              >harvest</span
            >
          </li>
        </ul>
      </card>
      <card class="bnp-staked">
        <div class="total">
          <h3>{{ farm.lpToken }} Tokens Staked</h3>
          <h4 v-if="isLogin"><CountUp :endVal="stakeVal.toNumber()" /></h4>
          <h4 v-else>Locked</h4>
        </div>
        <div class="action">
          <Button
            type="flat"
            :disabled="btnLoading"
            v-if="!isLogin"
            class="approve-btn"
            @click="$modal('wallet')"
            >Connect Wallet</Button
          >
          <Button
            type="flat"
            :disabled="btnLoading"
            v-else-if="!allowance.toNumber()"
            class="approve-btn"
            @click="handleApprove"
            >Approve {{ farm.symbol }}</Button
          >
          <div v-else class="act-group">
            <Button
              type="flat"
              :disabled="!stakeVal.toNumber()"
              class="approve-btn"
              @click="lpUnstake"
              >Unstake</Button
            >
            <Button
              type="flat"
              icon="plus"
              class="ml-10"
              @click="openDeposit"
            ></Button>
          </div>
        </div>
      </card>
    </div>
  </page>
</template>
<script>
import BigNumber from "bignumber.js";

export default {
  name: "menu-pool",
  $computed: {
    bnp: "bnp",
    pools: "bnp.pools",
    isLogin: "account.isLogin",
  },
  data() {
    return {
      farm: {},
      allowance: new BigNumber(0),
      stakeVal: new BigNumber(0),
      lpBalance: new BigNumber(0),
      btnLoading: false,
      harvestBnp: new BigNumber(0),
      harvestUni: new BigNumber(0),
      harvestSushi: new BigNumber(0),
    };
  },
  computed: {
    harvest() {
      return this.$s.bnp.harvest.map((i) => {
        const item = { ...i };
        switch (item.id) {
          case "bnp":
            item.num = this.harvestBnp;
            break;
          case "uni":
            item.num = this.harvestUni;
            break;
          case "sushi":
            item.num = this.harvestSushi;
            break;
        }
        return item;
      });
    },
  },
  async mounted() {
    const pid = Number(this.$route.params.id);
    const farm = this.pools.find((item) => pid === item.pid);
    if (!farm || this.$route.params.id === "") {
      this.$router.push("/menu");
      return;
    }
    this.farm = farm;

    this.lpContract = this.$s.bnp.getLpContract(this.farm.lpTokenAddress);
    // this.loadAllowance()
    // this.loadLpBalance()
    this.updateData();
    this.allowanceInternal = setInterval(() => {
      //   this.loadAllowance()
      //   this.loadLpBalance()
      this.updateData();
    }, 10000);
    this.stakeVal = await this.$s.bnp.fetchStakedValue(pid);
  },
  beforeDestroy() {
    clearInterval(this.allowanceInternal);
  },
  methods: {
    async loadAllowance() {
      this.allowance = await this.$s.bnp.fetchLPAllowance(
        this.farm.lpTokenAddress
      );
    },
    async loadLpBalance() {
      this.lpBalance = await this.$s.bnp.fetchTokenBalance(
        this.farm.lpTokenAddress
      );
    },
    async updateData() {
      const r = await Promise.all([
        this.$s.bnp
          .fetchLPAllowance(this.farm.lpTokenAddress)
          .catch(() => new BigNumber(0)),
        this.$s.bnp
          .fetchStakedValue(this.farm.pid)
          .catch(() => new BigNumber(0)),
        this.$s.bnp
          .fetchTokenBalance(this.farm.lpTokenAddress)
          .catch(() => new BigNumber(0)),
      ]);
      this.allowance = r[0];
      this.stakeVal = r[1];
      this.lpBalance = r[2];

      const harvest = await this.$s.bnp.fetchHarvest(this.farm.pid);
      this.harvestBnp = harvest.BNP;
      this.harvestUni = harvest.UNI;
      this.harvestSushi = harvest.SUSHI;
    },
    async handleApprove() {
      this.btnLoading = true;
      this.$s.bnp
        .lpApprove(this.farm.lpTokenAddress)
        .then((tx) => {
          return this.loadAllowance();
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.btnLoading = false;
          this.loadLpBalance();
        });
    },
    openDeposit() {
      this.$modal("deposit", {
        name: this.farm.lpToken.toUpperCase(),
        pid: this.farm.pid,
        max: this.lpBalance.toString(),
        // lpContract: this.lpContract,
        accept: async ({ val }) => {
          console.log(val);
          await this.onDespositConfirm(val);
          this.$modalClose();
          this.updateData();
        },
      });
    },
    async onDespositConfirm(val) {
      const tx = await this.$s.bnp.lpStake(this.farm.pid, val);
    },
    lpUnstake() {
      this.$modal("withdraw", {
        name: this.farm.lpToken.toUpperCase(),
        pid: this.farm.pid,
        max: this.stakeVal.toString(),
        accept: async ({ val }) => {
          await this.$s.bnp.lpUnStake(this.farm.pid, val);
          this.$modalClose();
          this.updateData();
        },
      });
    },
    doHarvest(id) {
      let harvest = new BigNumber(0);
      switch (id) {
        case "uni":
          harvest = this.harvestUni;
          if (!harvest.toNumber()) {
            return;
          }
          this.$s.bnp.harvestUni(this.farm.pid)
          break;
        case "bnp":
          harvest = this.harvestBnp;
          if (!harvest.toNumber()) {
            return;
          }
          this.$s.bnp.harvestBnp(this.farm.pid)
          break;
        case "sushi":
          harvest = this.harvestSushi;
          if (!harvest.toNumber()) {
            return;
          }
          this.$s.bnp.harvestSushi(this.farm.pid)
          break;
      }
      if (!harvest.toNumber()) {
        return;
      }
    },
  },
};
</script>
<style lang="less" scoped>
.top-inner {
  text-align: center;
  margin-top: 0px;
  .bnp-logo {
    img {
      width: 90px;
      height: 90px;
    }
  }
  .name {
    margin-top: 10px;
    font-size: 28px;
    font-weight: 600;
    color: #fff;
  }
}
.card-items {
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;

  max-width: 898px;
  width: 90%;
  .card {
    display: inline-block;
    width: 48%;
    margin-bottom: 30px;
    @media screen and (max-width: 1000px) {
      width: 100%;
      margin-right: 0;
    }
  }
}
.cions {
  margin-right: 4%;
  height: 364px;
  .harvest {
    padding-top: 33px;
    li {
      padding-bottom: 57px;
      &:last-of-type {
        padding-bottom: 0;
      }
    }
  }
}
.bnp-staked {
  vertical-align: top;
  height: 364px;
  .total {
    h3,
    h4 {
      margin: 0;
      color: #000;
      text-align: center;
      font-size: 22px;
      line-height: 30px;
    }
    h3 {
      margin-top: 13px;
    }
    h4 {
      height: 45px;
      line-height: 45px;
      font-size: 38px;
      margin-top: 52px;
    }
  }
  .action {
    text-align: center;
    margin-top: 62px;
    .approve-btn {
      width: 256px;
    }
  }
}
</style>
